export default {
  data() {
    return {
      trules: ""
    };
  },
  onLoad() {
    // 提现规则接口
    this.getRules();
  },
  methods: {
    // 提现规则接口
    getRules() {
      this.$http.get("/wallet/getRules", {}).then(res => {
        this.trules = res.data.trules;
      });
    }
  }
};
